// components/UpcomingEvents.tsx
const events = [
    {
        date: "14",
        month: "May",
        title: "CSE Fest",
        location: "University of Dhaka",
        type: "Tech",
        image: "/images/event1.png"
    },
    {
        date: "14",
        month: "May",
        title: "Cultural Night",
        location: "AIUB",
        type: "Cultural",
        image: "/images/event2.png"
    },
    {
        date: "14",
        month: "May",
        title: "Dev Hackathon",
        location: "BRAC University",
        type: "Hackathon",
        image: "/images/event3.png"
    },
];

export default function UpcomingEvents() {
    return (
        <section className="bg-white py-10">
            <div className="max-w-6xl mx-auto px-4">
                <h2 className="text-3xl font-bold text-center mb-8">Upcoming Events</h2>
                <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                    {events.map((event, idx) => (
                        <div key={idx} className="bg-gray-50 rounded-lg shadow hover:shadow-lg transition">
                            <img src={event.image} alt={event.title} className="w-full h-48 object-cover rounded-t-lg" />
                            <div className="p-4">
                                <div className="text-purple-600 font-bold text-sm mb-2">{event.type}</div>
                                <h3 className="text-xl font-semibold mb-1">{event.title}</h3>
                                <p className="text-sm text-gray-600">{event.location}</p>
                                <div className="mt-2 text-xs text-gray-500">{event.date} {event.month}</div>
                            </div>
                        </div>
                    ))}
                </div>
                <div className="text-center mt-8">
                    <button className="bg-purple-600 text-white px-6 py-2 rounded-md hover:bg-purple-700 transition">
                        See All Events
                    </button>
                </div>
            </div>
        </section>
    );
}