// components/PastEvents.tsx 
const pastEvents = [
    {
        title: "Tech Talk 2024", image: "/images/past1.jpg", description: "A packed auditorium enjoyed insights from leading developers."
    },
    {
        title: "Spring Fest", image: "/images/past2.jpg", description: "A cultural explosion of dance, music, and food from around the world."
    },
    {
        title: "Design Challenge", image: "/images/past3.jpg", description: "Creative minds competed to solve real-world design problems."
    }
];

export default function PastEvents() {
    return (
        <section className="bg-gray-50 py-12">
            <div className="max-w-6xl mx-auto px-4">
                <h2 className="text-3xl font-bold text-center mb-8">Past Events</h2>
                <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                    {pastEvents.map((event, idx) => (<div key={idx} className="bg-white rounded-lg shadow-md overflow-hidden"> <img src={event.image} alt={event.title} className="w-full h-48 object-cover" /> <div className="p-4">
                        <h3 className="text-xl font-semibold mb-2">{event.title}</h3>
                        <p className="text-gray-600 text-sm">{event.description}</p>
                    </div> </div>))}
                </div> </div> </section>);
}